#services{
    padding-top: 2rem;
    height: auto;
}
.services__container {
    padding-top: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 3rem;
}

.service {
    background: linear-gradient(black,var(--color-bg),black);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    height: fit-content;
    transition: var(--transition);
}

.service:hover {
    background: linear-gradient(var(--color-bg),black,var(--color-bg));
    border-color: var(--color-bg-variant);
    cursor: default;
    transition: var(--transition);
}

.service__head {
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgb(0, 0, 0, 0.1);
}
.service__head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list {
    padding: 2rem;
}
.service__list li{
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}
.service__list-icon{
    color: var(--color-primary);
    margin-top: 2px;
    font-size: 1.5rem;
}
.service__list p {
    font-size: 0.9rem;
}

/* ============== MEDIUM DEVICES =============== */

@media screen and (max-width:1200px) {
    #services{
        height: auto;
    }
    .services__container{
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
    .service {
        height: auto;
    }
}

/* ============== SMALL DEVICES =============== */

@media screen and (max-width:800px) {
    .services__container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}